.bar {
  width: 100%;
  height: 85px;
  background: #555271;
  position: fixed;
  top: 0px;
  z-index: 10;
  transition: all 0.5s;
}

.p-bar {
  display: inline;
  color: white;
  margin: 0px 10px 0px 10px;
  text-decoration: underline;
}

.naver {
  position: absolute;
  width: 100%;
  height: 85px;
  top: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-bar {
  height: 75px;
  margin: 5px;
  z-index: 2;
  position: absolute;
}

.UserIcon {
  position: absolute;
  top: 42px;
  transform: translateY(-50%);
  right: 15px;
  width: 60px;
  height: 60px;
  padding: 5px;
  border-radius: 140px !important;
  border-radius: 0px;
  color: white;
  z-index: 4;
}

.whatsapp {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background: #25d366;
  font-size: 35px;
  padding: 10px;
  border-radius: 50px;
  color: white;
  z-index: 4;
}

svg {
  width: 50px;
  background-size: 50px;
}

link {
  text-decoration: underline;
  color: white;
}

::-webkit-scrollbar {
  width: 8px; /* Tama単o del scroll en vertical */
  height: 8px; /* Tama単o del scroll en horizontal */
  display: none; /* Ocultar scroll */
}
