@import url("https://fonts.googleapis.com/css?family=Archivo:500|Open+Sans:300,700");

.texto-news {
  width: 40%;
  text-align: center;
  position: absolute;
  bottom: 12%;
  left: 10%;
  transform: translateX(0%);
}

.form-news {
  margin-top: 100px;
  width: 35%;
  position: absolute;
  right: 20px;
  bottom: 20%;
  margin-right: 20px;
  text-align: center;
}

.header-news {
  margin-top: 80px;
  width: 100%;
  height: calc(100vh - 80px);
  overflow: hidden;
  background-image: url(../components/img/contactoBack.jpeg);
  background-size: cover;
}

.overly-news {
  width: 100%;
  height: calc(100% - 80px);
  background: #4c3e66c5;
  z-index: 3;
  position: absolute;
  top: 80px;
}

.titulo_suscripcion {
  color: #351472;
  font-family: "Open Sans", sans-serif;
  transform: translateY(-50%);
  font-weight: 700;
  text-shadow: 3px 0px 0px #ffffff, 0px 3px 0px #ffffff, -3px 0px 0px #ffffff,
    0px -3px 0px #ffffff;
}

.sub{
  min-width: 100px !important;
}

.close{
  min-width: 50px !important;
  color: #6c757d;
}