.all-cursos {
  margin-top: 80px;
}

.header-cursos {
  width: 100%;
  height: calc(100vh - 80px);
  overflow: hidden;
  background-image: url(../components/img/cursos.jpg);
  background-size: cover;
}

.overly-header {
  width: 100%;
  height: calc(100% - 80px);
  background: #4c3e6690;
  z-index: 3;
  position: absolute;
  top: 80px;
}

.titulo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(50%);
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 2.5%;
  grid-row-gap: 50px;
  margin: 15vh 5% 0% 5%;
  width: 90%;
}

.cards-cursos {
  transition: all ease 0.5s;
  width: 90%;
  height: 350px;
  margin: 0px 0% 0% 0%;
  box-shadow: 0 0 16px rgb(0 0 0 / 10%);
  border-radius: 6px;
  position: relative;
  border: 1px solid #0000001a;
}

.cards-cursos:hover {
  transition: all ease 0.5s;
  transform: translateY(-5%);
  box-shadow: 0 0 25px rgb(0 0 0 / 20%);
  border-radius: 8px;
  border: 1px solid #00000033;
}
.titulo-cards {
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translateY(30%);
  color: #4c3e66;
}

.img-cursos_container {
  position: absolute;
  top: 10%;
  width: 100%;
  overflow: hidden;
  height: 45%;
  border-radius: 150px;
}

.cards-cursos img {
  width: 90%;
  position: relative;
  left: 50%;
  top: 20%;
  transform: translateX(-50%);
}

.texto-card_container {
  width: 90%;
  position: absolute;
  bottom: 2%;
  left: 5%;
  text-align: center;
  color: #7e5b96;
  font-size: 12px;
  font-weight: 600;
}

.btn-cursos:hover {
  transition: all 0.5s;
  background: #7e5b96;
}

.btn-cursos {
  transition: all 0.5s;
  background: #4c3e66;
  color: white;
  font-weight: bolder;
  border: none;
  outline: none;
  padding: 10px 20px 10px 20px;
  border-radius: 35px;
  margin-bottom: 10px;
}

.z-index {
  z-index: 5;
}
.ver-cursos {
  position: absolute;
  top: 85% !important;
  left: 50% !important;
  transform: translateX(-50%) !important;
  color: white;
  background: #2675ad;
  padding: 5px 10px 5px 10px;
  border-radius: 20px;
  text-decoration: none;
}

.ver-cursos:hover {
  color: white !important;
  cursor: pointer;
}

.scroll-btn {
  padding: 20px 100px 20px 100px;
  position: absolute;
  color: #ffffff;
  text-decoration: none;
}
.scroll-btn:hover {
  color: #ffffff;
  text-decoration: none;
}

.left,
.right {
  color: #4c3e66;
  width: 10px;
  opacity: 0.7;
  position: absolute;
  bottom: 50%;
  transform: translateY(25%);
}

.btn-slider {
  margin-top: 35%;
  bottom: -5px !important;
  min-height: 10px !important;
  min-width: 20px !important;
  color: #c7c7c7;
}

.btn-slider_right {
  float: right;
}

.degrade {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 10;
  background-image: linear-gradient(
    to right,
    #ffffffda,
    #00000000,
    #00000000,
    #00000000,
    #ffffffda
  ) !important;
}
