.header {
  width: 100%;
  height: calc(100vh - 85px);
  overflow: hidden;
  margin-top: 85px;
}
.background {
  width: 100%;
}

@media screen and (max-width: 700px) {
  .background {
    height: 100%;
    width: auto;
  }
  .logo {
    top: 18% !important;
    width: 80px !important;
  }
  .contenedor-texto-home {
    width: 95% !important;
    top: 30% !important;
  }
  .media {
    top: 100px !important;
    right: 10px !important;
    width: auto !important;
  }
  .whatsapp {
    right: 15px !important;
    bottom: 15px !important;
  }
  .next {
    height: 60vh !important;
  }
  .kjtUhL {
    min-height: 205px;
    text-align: center;
    width: 90% !important;
  }
  .slider-carrusel {
    width: 90% !important;
    left: 5% !important;
    top: 120px !important;
  }
  .filosofia {
    width: 90% !important;
    right: 5% !important;
    top: 70% !important;
  }
  .p-est{
    width: 90% !important;
  }
  .modal-dialog{
    top: 50% !important;
  }
  .cards {
    grid-template-columns: repeat(2, 1fr) !important;
    grid-column-gap: 2.5%;
    grid-row-gap: 50px;
    margin: 15vh 5% 0% 9% !important;
  }
  .texto-news{
    display: none !important;
  }
  .form-news{
    width: 90% !important;
    right: 0% !important;
  }
}

@media screen and (max-width: 500px) {
  .cards {
    grid-template-columns: repeat(1, 1fr) !important;
    grid-column-gap: 2.5%;
    grid-row-gap: 50px;
    margin: 15vh 5% 0% 9% !important;
  }

}

.overly {
  width: 100%;
  height: calc(100vh - 85px);
  position: absolute;
  z-index: 2;
  background: #4c3e6690;
}

.logo {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  z-index: 3;
}

.contenedor-texto-home {
  color: white;
  z-index: 3;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  text-align: center;
}

.texto-home {
  color: white;
  z-index: 3;
  font-size: 15px;
}

.newsletter-btn {
  z-index: 4;
  color: white;
  height: 30px;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  padding: 10px 5px 10px 5px;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.newsletter-btn button {
  width: 100%;
  height: 100%;
  outline: #2675ad;
}

.btn-modal {
  z-index: 4;
}

.newsletter-btn-est {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  background: #2675ad;
  border-radius: 20px;
  height: 30px;
  text-align: center;
}
.newsletter-btn-est p,
.newsletter-btn p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: white;
  width: 100%;
  z-index: -3;
}

.next {
  height: 50vh;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.overly-next {
  background-image: url("https://static.wixstatic.com/media/06f2ba_750888440ff94764a8b52b1d067d28f8~mv2.jpg/v1/crop/x_240,y_398,w_320,h_353,q_80/06f2ba_750888440ff94764a8b52b1d067d28f8~mv2.webp");
  width: 100%;
  height: 100%;
  opacity: 0.5;
  position: absolute;
}
.kjtUhL {
  min-height: 205px;
  text-align: center;
  width: 40% !important;
}

.kjtUhL h1 {
  position: absolute;
  left: 50%;
  top: 5%;
  color: #7e5b96;
  font-weight: 800;
  font-family: Georgia, "Times New Roman", Times, serif;
  transform: translateX(-50%);
}

.kjtUhL p {
  margin-top: 60px;
}

.slide-display {
  height: 100% !important;
  background: transparent;
  opacity: 1 !important;
}

.slide {
  height: 100% !important;
  opacity: 1 !important;
}

.estudios {
  width: 100%;
  height: 130vh;
  background-image: url(../components/img/background.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.texto-est {
  width: 100%;
  height: 40%;
  position: relative;
  background: #3a606f;
  text-align: center;
  color: #ffffff;
}

.p-est {
  width: 60%;
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translateX(-50%) translateY(-50%);
}

.overly-est {
  width: 100%;
  height: 60%;
  position: absolute;
  bottom: 0px;
  background: #3a606f85;
}

.titulo {
  color: #4c3e66;
  font-weight: 800;
}

.media {
  position: absolute;
  z-index: 5;
  top: 110px;
  right: 35px;
  width: 150px;
}

.media img {
  margin: 0px 5px 0px 5px;
}
